.fp-section.fp-table, .fp-slide.fp-table {
    display: table;
    table-layout: fixed;
    width: 100%;
}
.fp-section {
    position: relative;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.fp-tableCell {
    position: relative;
}
.fp-tableCell {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    height: 100%;
}
.fullpage__slide {
    width: 100%;
    height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    color: #fff;
}
.title {
    color: #fff;
    position: absolute;
    overflow: hidden;
    top: 50%;
    -webkit-transform: translate3d(0, -50%, 0);
    transform: translate3d(0, -50%, 0);
    z-index: 200;
    margin-left: 11%
}
.title__text {
    font-size: 8rem;
    letter-spacing: 0.2em;
    margin-bottom: 1.6rem;
    font-family: ui-monospace;
    font-weight: normal;
    text-shadow: 0 10px 30px rgb(2 11 22 / 50%);
}

.title .border {
    display: inline-block;
    margin-bottom: 3.2rem
}

.title .border span {
    display: block;
    width: 88px;
    height: 4px;
    border-radius: 5px;
    background: #ff4d5a;
}
.title .border span:first-child {
    margin-bottom: 1.4rem;
}
.title .border span:last-child {
    margin-left: 54px;
}

.title__lead {
    color: #fff;
    font-size: 2.2rem;
    line-height: 1.5;
    letter-spacing: 0.1em;
}
p {
    line-height: 2;
    word-wrap: break-word;
    white-space: normal;
}

.moon {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 29.427%;
    margin: auto;
    height: 1385px;
    width: 1385px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.moon__img {
    position: relative;
    width: 39.350180505%;
    height: 39.350180505%;
}

.layer {
    width: 100%;
    height: 100%;
}
.moon__front {
    z-index: 200;
}
.moon__text-wrap {
    position: absolute;
    left: -15.5963%;
    top: 50%;
    -webkit-transform: translate3d(0, -50%, 0);
    transform: translate3d(0, -50%, 0);
}
.moon__text {
    font-size: 4.1rem;
    letter-spacing: 0.1em;
    text-shadow: 0 10px 15px rgb(2 11 22 / 20%);
    color: #ff4d5a;
    font-family: "futura_b";
}

.moon__back {
    z-index: -100;
}

